:root {
  --default: #1b1b1b;
  --labelDefault: #a8b0bf;
  --inputDefault: #e8f0fe;
  --defaultIcon: #686d77;
  --borderIcon: rgba(0, 0, 0, 0.1);
  --borderCheckbox: #d3dae6;
  --primaryHover: #1e964e;
  --linkHover: #005924;
  --backgroundFooter: #f5f7fa;
  --shadow: rgba(97, 44, 21, 0.19);
  --blue: #2d6eed;
  --blueLight: #3d8bfd;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e8b8f5;
  --red: #dc3545;
  --orange: #fec410;
  --yellow: #000;
  --green: #b0e43f;
  --teal: #20c997;
  --cyan: #49ffc3;
  --white: #fff;
  --gray-dark: #343a40;
  --secondary: #686d77;
  --danger: #e63e46;
  --dark: #203259;
  --primary: #22ab59;
  --success: #15d164;
  --info: #17a2b8;
  --warning: #de9100;
  --light: #f2f4f7;
  --gray: #7f7e7e;
  --black: #000000;
}
body {
  margin: 0;
  font-family: "Roboto",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  color: var(--default);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh !important;
}